<template>
  <div class="sendMsg">
    <hr style="margin-bottom:20px;">
    <form action>
      <div class="userinfo">
        <label for="username">昵称:</label>
        <input
          type="text"
          id="username"
          autocomplete="off"
          v-model="message.username"
          placeholder="访客"
        >
        <br>
        <br>
        <label for="title">标题:</label>
        <input
          type="text"
          id="title"
          autocomplete="off"
          v-model="message.title"
          placeholder="无标题"
        >
      </div>
      <br>
      <div class="avatar">
        <span>请选择头像：</span>
        <CanvasSelectAvatar
          @hiddenAvatarList='hiddenAvatarList'
          @setAvatarName='setAvatarName'
        />
        <ul
          class="list"
          v-show="isShowAvatarList"
        >
          <li
            class="item"
            v-for="(item,i) in avatarSum"
            :key="i"
          >
            <input
              ref="avatar"
              class="ra"
              type="radio"
              name="avatar"
              @click="selectedAvatar(item)"
            >
            <img :src="require(`../../assets/img/message/avatar/${item}.gif`)">
          </li>
        </ul>
      </div>
      <div class="content">
        <h4>
          留言内容 (
          <span class="red">必填</span>)
        </h4>
        <textarea
          name="msg"
          cols="50"
          rows="10"
          v-model="message.msg"
        ></textarea>
        <p id="silent">
          <span>是否为悄悄话(只有管理员才能看得见):</span>
          <input
            type="checkbox"
            v-model="message.isSecret"
          >
        </p>
        <button
          id="send"
          @click.prevent="send"
        >发表</button>
      </div>
    </form>
  </div>
</template>


<script>
import config from "../../config/config";
import CanvasSelectAvatar from "./CanvasSelectAvatar";
export default {
  data() {
    return {
      avatarSum: ["11", "15", "18", "04"], //头像的总编号数
      message: {
        username: "",
        title: "",
        avatar: "",
        msg: "",
        isSecret: undefined, //是否为悄悄话
        isUploadAvatar: '0',
      },
      showSeletAvatar: false,
      isShowAvatarList: true,
      inputFile: ''
    };
  },
  components: {
    CanvasSelectAvatar
  },
  mounted() {
    this.$refs.avatar[0].checked = true;
    this.message.avatar = this.avatarSum[0];
  },
  methods: {
    hiddenAvatarList(boolean) {
      this.isShowAvatarList = boolean
    },
    selectedAvatar(i) {
      this.message.avatar = i
    },
    setAvatarName(filename) {
      //接收从CanvasSelectAvatar传过来的头像名，并写入message中
      this.message.avatar = filename
      this.message.isUploadAvatar = '1'
    },

    send() {
      if (!this.message.msg) {
        return alert("请输入留言信息");
      }

      fetch(`${config.url}/addmsg`, {
        method: "post",
        headers: {
          "Content-Type": "application/json"
        },

        body: JSON.stringify(this.message)
      })
        .then(res => {
          return res.json();
        })
        .then(data => {
          if (!data.code) {
            this.message.msg = "";
            this.message.isSecret = undefined;
            alert(data.data);
            this.$emit("refreshMsg");
            window.scrollTo(0, 210); //滚动窗口到指定坐标
          }
        });
    },
  }
};
</script>


<style lang='stylus' scoped>
.red 
  color: red
.sendMsg 
  width 100%
  margin-top 20px
  box-sizing border-box
  font-size 1.4rem
  .userinfo 
    #username, #title 
      padding: 3px 5px
  .avatar 
    #selectAvatar
      padding 5px 0
    .list 
      display: flex;
      flex-wrap: wrap;
      .item 
        margin: 10px 15px;
        .ra 
          width: 15px
          height: 15px
          vertical-align: top
          margin: 20px 10px 0 0
          &:hover 
            cursor: pointer

  .content 
    position relative
    margin-top 10px
    h4 
      margin-bottom: 10px
    textarea 
      padding: 5px
      text-indent: 1em
    #silent 
      margin: 10px 0
      input 
        width: 15px;
        height: 15px;
        vertical-align: middle
        &:hover 
          cursor: pointer
    #send 
      padding: 0 5px
      &:hover 
        cursor: pointer


@media screen and (max-width: 480px) {
  .sendMsg {
    padding: 5px;
  }
}
</style>
